<template>
  <div>
    <div class="value_box " style="margin-bottom: 20px;">
      <div class="value_box">
        <span>岗位名称：</span>
         <el-select v-model="OptionData.positionName" class="w200" placeholder="请选择">
    <el-option
      v-for="item in AddArr"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
      </div>
       <div class="value_box">
        <span>船舶类型：</span>
           <el-select v-model="OptionData.shipType" class="w200" placeholder="请选择">
    <el-option
      v-for="item in getShipTypeList"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
      </div>
       <div class="value_box">
        <span>职位状态：</span> <el-input class="w200"  placeholder="请输入内容"></el-input>
      </div>
       <el-button type="primary" @click="getData">查询</el-button>
       <el-button type="primary" @click="AddClick">新增岗位</el-button>
    </div>
  <el-table
    :data="tableData"
    style="width: 100%"
    >
    <el-table-column
      prop="positionName"
      label="岗位名称"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="contractDuration"
      label="合同时长"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="education"
      label="要求学历"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="monthlySalary"
      label="月薪"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="positionNum"
      label="招聘人数"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="shipType"
      label="船舶类型"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="updateTime"
      label="发布时间"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="statusFlag"
      label="职位状态"  align="center"
      >
       <template slot-scope="item">
         <div>{{item.row.statusFlag==0?'未启用':'已启用'}}</div>
      </template>
    </el-table-column>

     <el-table-column label="操作" width="300" align="center">
      <template slot-scope="item">
         <el-button type="danger" v-if="item.row.statusFlag==='1'"
          size="mini" @click="operate(item.row.id,0)"
          >停用</el-button>
           <el-button type="primary"  @click="operate(item.row.id,1)" v-if="item.row.statusFlag==='0'"
          size="mini"
          >启用</el-button>
        <el-button type="primary"
          size="mini"
          >编辑</el-button>
          <el-button type="danger"
          size="mini"
          >删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="fy">
    <el-pagination
  background @current-change="pageClick"
  layout="prev, pager, next"
  :total="total">
</el-pagination>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      jobStatus: [
        { label: '未启用', value: '0' },
        { label: '已启用', value: '1' }
      ],
      total: 0,
      tableData: [

      ],
      OptionData: {
        pageNum: 1,
        pageSize: 10
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    operate (id, value) {
      this.$axios.post('/seaman-recruit/position/updateById', { statusFlag: value, id: id, companyId: JSON.parse(localStorage.getItem('user')).id }).then(res => {
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: '操作成功!'
          })
          this.getData()
          // this.$router.back()
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    pageClick (value) {
      this.OptionData.pageNum = value
      this.getData()
    },
    getData () {
      this.$axios.post('/seaman-recruit/position/queryByPage', this.OptionData).then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.list
          this.total = res.data.data.total
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    AddClick () {
      this.$router.push('/home/JobListings/Add')
    },
    formatter (row, column) {
      return row.address
    }
  }
}
</script>

<style lang="scss" scoped>
.value_box{
  display: flex;
  align-items: baseline;
}

::v-deep.el-table th.el-table__cell{
  background: #F8F8F9;
}
.w200{
  width: 140px;
  margin: 0 10px;
}
</style>
