<template>
  <div>
    <h1 style="margin-bottom: 20px;">新增岗位</h1>
  <el-form ref="form" :model="form" label-width="120px" style="width: 100%%;">
      <el-form-item label="职位名称：">
        <el-select v-model="form.positionName" class="w100" placeholder="请选择">
    <el-option
      v-for="item in AddArr"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="学历要求：">
    <el-input v-model="form.education"></el-input>
  </el-form-item>
      <el-form-item label="月薪：">
    <el-input v-model="form.monthlySalary" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="船龄(年)：">
    <el-input v-model="form.shipAge"></el-input>
  </el-form-item>
      <el-form-item label="船舶类型：">
   <el-select v-model="form.shipType" class="w100" placeholder="请选择">
    <el-option
      v-for="item in getShipTypeList"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="具体航线：">
    <el-input v-model="form.specificRoute"></el-input>
  </el-form-item>
      <el-form-item label="合同时长(年)：">
    <el-input v-model="form.contractDuration"></el-input>
  </el-form-item>
      <el-form-item label="上船地点：">
    <el-input v-model="form.boardingPlace"></el-input>
  </el-form-item>
      <el-form-item label="招聘人数：">
    <el-input v-model="form.positionNum" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="总吨/马力：">
    <el-input v-model="form.grossTon" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
  </el-form>
  <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="BtnBack">取消</el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick">提交</el-button>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {

      }
    }
  },
  methods: {
    BtnBack () {
      this.$router.back()
    },
    BtnClick () {
      const datas = JSON.parse(JSON.stringify(this.form))
      datas.userName = JSON.parse(localStorage.getItem('user')).userName
      datas.userId = JSON.parse(localStorage.getItem('user')).id
      this.$axios.post('/seaman-recruit/position/add', datas).then(res => {
        if (res.data.code === 200) {
          this.$message({
            message: '操作成功！',
            type: 'success'
          })
          this.$router.go(-1)
        } else {
          this.$message.error(res.data.data)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
