<template>
  <div>
    <div class="value_box " style="margin-bottom: 20px;">
      <div class="value_box">
        <span>航海经验：</span> <el-input v-model="OptionData.experienceSailing" class="w200"  placeholder="请输入内容"></el-input>
      </div>
       <div class="value_box">
        <span>证书等级：</span> <el-input class="w200" v-model="OptionData.certificateLevel"  placeholder="请输入内容"></el-input>
      </div>
       <div class="value_box">
        <span>要求月薪：</span> <el-input class="w200" v-model="OptionData.monthlySalary"  placeholder="请输入内容"></el-input>
      </div>
       <div class="value_box">
        <span>应聘职位：</span> <el-input class="w200" v-model="OptionData.jobApplication"  placeholder="请输入内容"></el-input>
      </div>

       <el-button type="primary">查询</el-button>
    </div>
  <el-table
    :data="tableData"
    style="width: 100%"
    >
    <el-table-column
      prop="appellation"
      label="称谓"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="major"  align="center"
      label="专业"
      >
    </el-table-column>
    <el-table-column
      prop="education"  align="center"
      label="学历"
      >
    </el-table-column>
    <el-table-column
      prop="certificateLevel"  align="center"
      label="证书等级"
      >
    </el-table-column>
    <el-table-column
      prop="monthlySalary"  align="center"
      label="要求月薪"
      >
    </el-table-column>
    <el-table-column
      prop="originalPosition"  align="center"
      label="原任职务"
      >
    </el-table-column>
    <el-table-column
      prop="currentResidence"  align="center"
      label="现居住地"
      >
    </el-table-column>
     <el-table-column label="操作" align="center">
      <template slot-scope="">
        <el-button type="primary"
          size="mini"
          >查看</el-button>

      </template>
    </el-table-column>
  </el-table>
<div class="fy">
    <el-pagination
  background @current-change="pageClick"
  layout="prev, pager, next"
  :total="total">
</el-pagination>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [

      ],
      total: 0,
      OptionData: {
        userType: 0,
        pageNum: 1,
        pageSize: 10
      }
    }
  },
  methods: {
    pageClick (value) {
      this.OptionData.pageNum = value
      this.getData()
    },
    gitData () {
      this.$axios.post('/seaman-recruit/resume/queryByPage', this.OptionData).then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.list
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    formatter (row, column) {
      return row.address
    }

  },
  created () {
    this.gitData()
  }
}
</script>

<style lang="scss" scoped>
.value_box{
  display: flex;
  align-items: baseline;
}

::v-deep.el-table th.el-table__cell{
  background: #F8F8F9;
}
.w200{
  width: 140px;
  margin: 0 10px;
}
</style>
