<template>
  <div>
    <div class="value_box " style="margin-bottom: 20px;">
      <div class="value_box">
        <span>用户名称：</span> <el-input class="w200" v-model="OptionData.userName"  placeholder="请输入内容"></el-input>
      </div>
       <div class="value_box">
        <span>用户邮箱：</span> <el-input class="w200" v-model="OptionData.email"  placeholder="请输入内容"></el-input>
      </div>
       <el-button type="primary" @click="getData">查询</el-button>
    </div>
  <el-table
    :data="tableData"
    style="width: 100%"
    >
    <el-table-column
      prop="createdTime"
      label="创建时间"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="userName"  align="center"
      label="用户名"
      >
    </el-table-column>
    <el-table-column
      prop="userType"  align="center"
      label="用户角色"
      >
       <template slot-scope="item">
       <div>{{item.row.userType==0?'船员用户':item.row.userType==1?'企业用户':'超级管理员'}}</div>
      </template>
    </el-table-column>
    <el-table-column
      prop="email"  align="center"
      label="电子邮箱"
      >
    </el-table-column>

     <el-table-column label="操作"  width="200" align="center">
      <template slot-scope="item">

          <el-button @click="open(item.row.id)" type="danger"
          size="mini"
          >删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="fy">
    <el-pagination
  background @current-change="pageClick"
  layout="prev, pager, next"
  :total="total">
</el-pagination>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      total: 0,
      tableData: [

      ],
      OptionData: {
        pageNum: 1,
        pageSize: 10
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    pageClick (value) {
      this.OptionData.pageNum = value
      this.getData()
    },
    open (value) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post('/seaman-recruit/user/delete', [value]).then(res => {
          if (res.data.code === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.getData()
          } else {
            this.$message.error(res.data.data)
          }
        })
      }).catch(() => {

      })
    },
    getData () {
      this.$axios.post('/seaman-recruit/user/queryByPage', this.OptionData).then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.records
          this.total = res.data.data.total
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    formatter (row, column) {
      return row.address
    }
  }
}
</script>

<style lang="scss" scoped>
.value_box{
  display: flex;
  align-items: baseline;
}

::v-deep.el-table th.el-table__cell{
  background: #F8F8F9;
}
.w200{
  width: 140px;
  margin: 0 10px;
}
</style>
