<template>
  <div>
    <Template v-if="form" title="企业详情" :form="form" />
    <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="$router.back()">取消</el-button>
    <el-button type="danger" size="small" class="btn_r" @click="operate(2)">拒绝</el-button>
  <el-button type="primary" size="small" class="btn_r" @click="operate(1)">通过</el-button>
  </div>
  </div>
</template>

<script>
import Template from '@/components/companyTemplate'
export default {
  components: { Template },
  data () {
    return {
      form: null
    }
  },
  created () {
    this.getData()
  },
  methods: {
    operate (value) {
      this.$axios.post('/seaman-recruit/company/updateById', { statusFlag: value, id: this.$route.query.id }).then(res => {
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: '审核成功!'
          })
          this.$router.back()
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    getData () {
      this.$axios.get('/seaman-recruit/company/', this.$route.query.id).then(res => {
        if (res.data.code === 200) {
          if (res.data.data) {
            this.form = res.data.data
          }
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    }
  }
}
</script>

