<template>
  <div class="home_box">
<el-container>
  <el-header class="header_box">
    <div class="title">
     海员招聘
    </div>
     <div class="avatar_box">
   <el-avatar :size="30"  ><img v-if="urlImg " :src="urlImg" style="width: 100%;" alt=""></el-avatar>
   <div class="nanfont_sezi">{{userName}}</div>
     <el-button type="primary" size="small" @click="back">退出</el-button>
    </div>
  </el-header>
  <!-- 菜单 -->
  <el-container>
    <el-aside width="200px">
    <el-menu style="    height: 100%;"
      class="el-menu-vertical-demo"
      :default-active="$route.name"
   >
      <el-menu-item v-for="v,i in meunItem" :index="v.url" :key="i" @click="menuClick(v.url)" >
     <span slot="title">{{v.title}}</span>
      </el-menu-item>
    </el-menu>
    </el-aside>
    <!-- 菜单end -->
    <!-- 可视区域 -->
    <el-main>
      <div>
        <el-card >
     <router-view/>
    </el-card>
      </div>
    </el-main>
    <!--可视区域end  -->
  </el-container>
</el-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 企业
      meunItem: [

      ],
      urlImg: '',
      userName: ''
      // 管理员
      // meunItem: [
      //   { title: '企业审核', url: '/home/auditList' },
      //   { title: '企业管理', url: '/home/enterpriseList' }, { title: '用户管理', url: '/home/userList' }
      // ],
      // 用户
      // meunItem: [
      //   { title: '岗位列表', url: '/home/user/JobList' },
      //   { title: '个人中心', url: '/home/user/personalCenter' },
      //   { title: '我的简历', url: '/home/user/myResume' }
      // ]

    }
  },
  methods: {
    back () {
      const backlen = window.history.length - 1
      window.history.go(-backlen)
      localStorage.removeItem('user')
      this.$router.replace({ path: '/' })
    },
    menuClick (url) {
      console.log(url)
      this.$router.push(url)
    }
  },
  created () {
    this.urlImg = JSON.parse(localStorage.getItem('user')).headPortrait ? 'http://' + JSON.parse(localStorage.getItem('user')).headPortrait : false
    console.log(this.urlImg)
    this.userName = JSON.parse(localStorage.getItem('user')).userName
    const index = JSON.parse(localStorage.getItem('user')).userType
    console.log(index)
    switch (index) {
      case 0:
        this.meunItem = [
          { title: '岗位列表', url: '/home/user/JobList' },
          { title: '个人中心', url: '/home/user/personalCenter' },
          { title: '我的简历', url: '/home/user/myResume' }
        ]
        break
      case 1:
        this.meunItem = [
          { title: '职位列表', url: '/home/JobListings' },
          { title: '船员列表 ', url: '/home/crewList' },
          { title: '企业中心', url: '/home/EnterpriseCenter' },
          { title: '求职列表', url: '/home/searchList' }
        ]
        break
      case 2:
        this.meunItem = [
          { title: '企业审核', url: '/home/auditList' },
          { title: '企业管理', url: '/home/enterpriseList' }, { title: '用户管理', url: '/home/userList' }
        ]
        break
    }
    if (!JSON.parse(localStorage.getItem('router'))) return
    localStorage.removeItem('router')
    this.$router.push(this.meunItem[0].url).catch(err => err)
  }
}
</script>

<style lang="scss" scoped>

.title{
      font-size: 21px;
    color: #409EFF;
    font-style: oblique;
}
.nanfont_sezi{
  margin: 0 6px;
  font-size: 16px;
font-weight: 500;
color: #333333;
}
.avatar_box{
  display: flex;
  align-items: center;
}
.header_box{
  border-bottom: 1px solid #f3f3f3;
    display: flex;
    justify-content: space-between;
    background-color: #fff !important

}
 .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    padding: 10px;
    background-color: #EBEEF5;
  }

 .el-container{
   height: 100%;
 }
  .home_box{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;left: 0;
  }
</style>
