<template>
  <div>
    <div class="title_box">
     <h1> 修改密码</h1>
    </div>

    <el-form ref="form" :model="form" label-width="120px">
      <el-form-item label="旧密码：">
    <el-input class="w200" v-model="form.oldPassword"></el-input>
  </el-form-item>
      <el-form-item label="新密码：">
    <el-input class="w200" v-model="form.newPassword"></el-input>
  </el-form-item>
      <el-form-item label="确认密码：">
    <el-input class="w200" v-model="form.Confirmpassword"></el-input>
  </el-form-item>
  </el-form>
     <div >
  <el-button class="btn_l" size="small" @click="BtnBack">取消</el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick">提交</el-button>
  </div>
    <el-divider></el-divider>
      <h1 > 修改头像</h1>
      <el-upload
      class="avatar-uploader"
      action="http://172.81.209.8:9527/seaman-recruit/test/fileUrl"
      :show-file-list="false"
      :http-request="uploadFile"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload">
      <img v-if="imageUrl" :src="imageUrl" referrerpolicy="no-referrer" class="avatar">
      <i v-else class="el-icon-plus  avatar-uploader-icon"></i>
    </el-upload>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {},
      imageUrl: JSON.parse(localStorage.getItem('user')).headPortrait ? 'http://' + JSON.parse(localStorage.getItem('user')).headPortrait : ''
    }
  },
  methods: {
    uploadFile (params) {
      const formdata = new FormData()
      const file = params.file
      formdata.append('file', file)
      this.$axios.post('/seaman-recruit/user/pictureUpload', formdata).then(res => {
        if (res.data.code === 200) {
          this.imageUrl = 'http://' + res.data.data
          this.$message({
            message: '修改头像成功！',
            type: 'success'
          })
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    BtnClick () {
      const datas = JSON.parse(JSON.stringify(this.form))
      datas.userName = JSON.parse(localStorage.getItem('user')).userName
      this.$axios.post('/seaman-recruit/user/updatePassword', datas).then(res => {
        if (res.data.code === 200) {
          this.$message({
            message: '操作成功！',
            type: 'success'
          })
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      console.log(file)
    },
    BtnBack () {
      this.$router.back()
    }
  }
}
</script>

<style lang="scss" scoped>
.w200{
  width: 400px;
}
.avatar{
  width: 200px;
  height: 200px;
}
  ::v-deep.avatar-uploader .el-upload {
    margin-top: 20px;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
.l_box{
  display: inline-block;
  margin-bottom: 25px;
  width: 100px;
}
.btn_l{
  margin-right: 30px;
}
.title_box{
  margin-bottom: 30px;
}
</style>
