<template>
  <div>
    <h1 style="margin-bottom: 20px;">企业中心</h1>
    <div v-if="form.statusFlag!==undefined" style="margin-bottom: 20px;">公司状态：
      <span v-if="form.statusFlag===0"> 审核中</span>
      <span v-if="form.statusFlag===1" style="color:#67C23A">正常招聘 </span>
      <span v-if="form.statusFlag===2" style="color:#F56C6C"> 公司已被停用，请联系相关管理员！</span>
      </div>
  <el-form ref="form" :model="form" label-width="120px">
      <el-form-item label="公司名称">
    <el-input v-model="form.companyName" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="公司性质">
    <el-input v-model="form.companyType" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="联系人">
    <el-input v-model="form.contactsName" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="联系手机">
    <el-input v-model="form.contactsPhone" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="电子邮箱">
    <el-input v-model="form.email" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="员工人数">
    <el-input v-model="form.employeesNumber" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="地址">
    <el-input v-model="form.address" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="注册资金">
    <el-input v-model="form.capital" :disabled="form.statusFlag===2" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="公司简介">
    <el-input v-model="form.companyDesc" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>

      <el-form-item label="传真">
    <el-input v-model="form.fax" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="固定电话">
    <el-input v-model="form.phone" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="法定代表人">
    <el-input v-model="form.representative" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="社会信用代码">
    <el-input v-model="form.socialCreditCode" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="公司网址">
    <el-input v-model="form.website" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="成立年份">
         <el-date-picker :disabled="form.statusFlag===2"
                v-model="form.year"
                type="date" style="width: 100%;"
                placeholder="选择成立时间">
              </el-date-picker>
  </el-form-item>
  </el-form>
  <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="BtnBack">取消</el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick" :disabled="form.statusFlag==2">提交</el-button>
  </div>
   <el-divider></el-divider>
      <h1 > 修改头像</h1>
      <el-upload
      class="avatar-uploader"
      action="http://172.81.209.8:9527/seaman-recruit/test/fileUrl"
      :show-file-list="false"
      :http-request="uploadFile"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload">
      <img v-if="imageUrl" :src="imageUrl" referrerpolicy="no-referrer" class="avatar">
      <i v-else class="el-icon-plus  avatar-uploader-icon"></i>
    </el-upload>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {
        companyName: '',
        companyType: '',
        contactsName: '',
        contactsPhone: '',
        email: '',
        employeesNumber: '',
        address: '',
        capital: '',
        fax: '',
        phone: '',
        representative: '',
        socialCreditCode: '',
        website: '',
        year: ''
      },
      imageUrl: JSON.parse(localStorage.getItem('user')).headPortrait ? 'http://' + JSON.parse(localStorage.getItem('user')).headPortrait : '',
      url: '/seaman-recruit/company/add'
    }
  },
  created () {
    this.getData()
  },
  methods: {

    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      console.log(file)
    },
    uploadFile (params) {
      const formdata = new FormData()
      const file = params.file
      formdata.append('file', file)
      this.$axios.post('/seaman-recruit/user/pictureUpload', formdata).then(res => {
        if (res.data.code === 200) {
          this.imageUrl = 'http://' + res.data.data
          this.$message({
            message: '修改头像成功，重新登录后生效！',
            type: 'success'
          })
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    tiem (chinaStandard) {
      var date = new Date(chinaStandard)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? ('0' + m) : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      const time = y + '-' + m + '-' + d

      return time
    },
    BtnBack () {
      this.$router.back()
    },
    getData () {
      this.$axios.get('/seaman-recruit/company/queryByUserId/', JSON.parse(localStorage.getItem('user')).id).then(res => {
        if (res.data.code === 200) {
          if (res.data.data && res.data.data.length > 0) {
            this.form = res.data.data[0]
            // debugger
            this.url = '/seaman-recruit/company/updateById' // 判断修改还是新增
          }
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    BtnClick () {
      this.form.year = this.tiem(this.form.year)
      this.form.userId = JSON.parse(localStorage.getItem('user')).id
      this.$axios.post(this.url, this.form).then(res => {
        if (res.data.code === 200) {
          this.$message({
            message: '操作成功！',
            type: 'success'
          })
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  ::v-deep.avatar-uploader .el-upload {
    margin-top: 20px;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
