          <el-input type="password" placeholder="确认密码" autoComplete="off" v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="login-btn" type="primary" @click="userRegister">注册</el-button>
