    <!-- 个人注册END -->
    <!-- 企业注册 -->
         <div class="login-main" style="width: 880px;" v-if="typeBtn===1">
      <h3 class="login-title">企业注册</h3>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
        <el-row :gutter="20">
           <el-col :span="6">
             <el-form-item prop="userName">
            <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.userName" placeholder="用户名" autoComplete="off" ></el-input>
        </el-form-item>
          </el-col>
            <el-col :span="6">
           <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.email" placeholder="邮箱" autoComplete="off"></el-input>
        </el-form-item>
          </el-col>
            <el-col :span="6">
          <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.enterpriseName" placeholder="企业名称" autoComplete="off"></el-input>
        </el-form-item>
          </el-col>

           <el-col :span="6">
            <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.legalRepresentative" placeholder="法定代表人" autoComplete="off"></el-input>
        </el-form-item>
          </el-col>
           <el-col :span="6">
                <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.nature" placeholder="公司性质" autoComplete="off"></el-input>
        </el-form-item>
          </el-col>
           <el-col :span="6">
              <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.socialCreditCode" placeholder="社会信用码" autoComplete="off"></el-input>
        </el-form-item>
          </el-col>
        <el-col :span="6">
           <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
              <el-date-picker
                v-model="addFrom.establishmentTime"
                type="date" style="width: 205px;"
                placeholder="选择成立时间">
              </el-date-picker>
        </el-form-item>
          </el-col>
            <el-col :span="6">
             <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.registeredCapital" placeholder="注册资金" autoComplete="off"></el-input>
        </el-form-item>
          </el-col>
            <el-col :span="6">
             <el-form-item prop="email">
             <el-input type="text" style="display:none"></el-input>
          <el-input v-model="addFrom.contact" placeholder="联系方式" autoComplete="off"></el-input>
        </el-form-item>
          </el-col>
           <el-col :span="6">
                 <el-form-item prop="password">
            <el-input type="text" style="display:none"></el-input>
          <el-input type="password" placeholder="密码" autoComplete="off" v-model="addFrom.password" @keyup.enter.native="submitForm('addFrom')"></el-input>
        </el-form-item>
          </el-col>
           <el-col :span="6">
                  <el-form-item prop="password">
              <el-input type="text" style="display:none"></el-input>
          <el-input type="password" placeholder="确认密码" autoComplete="off" v-model="addFrom.password" @keyup.enter.native="submitForm('addFrom')"></el-input>
        </el-form-item>
          </el-col>
        </el-row>
          <el-form-item>
          <el-button class="login-btn" type="primary" @click="Addregister">注册</el-button>
          <div class="btn_box"> <div style="display: flex;"><div @click="typeBtn=3">已有账号？去登录</div></div></div>
        </el-form-item>

