    </div>
    <!-- 企业注册end -->
    <!-- 忘记密码 -->
     <div class="login-main" v-if="typeBtn===2">
      <h3 class="login-title">忘记密码</h3>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
        <el-form-item prop="username">
          <el-input v-model="ruleForm.username" placeholder="用户名"></el-input>
        </el-form-item>
         <el-form-item prop="Mail" >
          <div style="display: flex;">
            <el-input v-model="ruleForm.Mail" placeholder="验证码"></el-input> <el-button type="primary" style='margin-left: 10px;'>发送验证码</el-button>
          </div>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" placeholder="密码" v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')"></el-input>
        </el-form-item>
          <el-form-item prop="password">
          <el-input type="password" placeholder="确认密码" v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="login-btn" type="primary" @click="submitForm('ruleForm')">登录</el-button>
          <div class="btn_box"> <div style="display: flex;"><div @click="typeBtn=3">已有账号？去登录</div></div></div>
        </el-form-item>
      </el-form>
    </div>
    <!-- 忘记密码end -->
    <canvas id="canvas" class="login-anim"></canvas>
  </div>
</template>

<script>
export default {
  name: 'login',
  data: function () {
    var checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error('邮箱不能为空'))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error('请输入正确的邮箱格式'))
        }
      }, 100)
    }

    return {
      addFrom: {},
      typeBtn: 3,
      ruleForm: {
        password: '',
        userName: '',
        verificationCode: ''
      },
      rules: {
        Mail: [{ required: true, trigger: 'blur', validator: checkEmail }],

        userName: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    Addregister () {
      this.addFrom.userType = this.typeBtn
      this.$axios.post('/seaman-recruit/user/register', this.addFrom).then(res => {
        if (res.data.code === 200) {
          this.$message({
            message: '注册成功！',
            type: 'success'
          })
          this.typeBtn = 3
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    userRegister () { // 用户注册
      this.ruleForm.userType = this.typeBtn
      this.$axios.post('/seaman-recruit/user/register', this.ruleForm).then(res => {
        if (res.data.code === 200) {
          this.$message({
            message: '注册成功！',
            type: 'success'
          })
          this.typeBtn = 3
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    submitForm () { // 登录
      this.$axios.post('/seaman-recruit/user/login', this.ruleForm).then(res => {
        if (res.data.code === 200) {
          this.$axios.get('/seaman-recruit/user/currentLoginUser', '').then(data => {
            if (res.data.code === 200) {
              this.$message({
                message: '登录成功，页面跳转中',
                type: 'success'
              })
              res.data.data.id = data.data.data.id
              res.data.data.headPortrait = data.data.data.headPortrait
              console.log(data, res)
              localStorage.setItem('router', true)
              localStorage.setItem('user', JSON.stringify(res.data.data))
              this.$router.push('/home')
            } else {
              this.$message.error(data.data.data)
            }
          })
        } else {
          this.$message.error(res.data.data)
        }
      })
    }
  },
  mounted () {
    // 实现页面底部波纹特效
    const canvas = document.getElementById('canvas')
    const ctx = canvas.getContext('2d')
    canvas.width = canvas.parentNode.offsetWidth
    canvas.height = 500
    console.log(canvas.width, canvas.height)

    // 如果浏览器支持requestAnimFrame则使用requestAnimFrame否则使用setTimeout
    window.requestAnimFrame = (function () {
      return (
        window.requestAnimationFrame ||
        window.webkitRequestAnimationFrame ||
        window.mozRequestAnimationFrame ||
        window.oRequestAnimationFrame ||
        window.msRequestAnimationFrame ||
        function (callback) {
          window.setTimeout(callback, 1000 / 60)
        }
      )
    })()

    // 波浪大小
    const boHeight = 40 // canvas.height / 10;
    const posHeight = canvas.height - 150 // canvas.height / 1.2;

    // 初始角度为0
    let step = 0
    // 定义三条不同波浪的颜色
    const lines = [
      'rgba(22, 123, 255, 0.2)',
      'rgba(0, 165, 255, 0.6)',
      'rgba(0, 122, 255, 0.4)'
    ]

    function loop () {
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      step++
      // 画3个不同颜色的矩形
      for (let j = lines.length - 1; j >= 0; j--) {
        ctx.fillStyle = lines[j]

        // 每个矩形的角度都不同，每个之间相差45度
        const angle = ((step + j * 70) * Math.PI) / 180 // 50
        const deltaHeight = Math.sin(angle) * boHeight
        const deltaHeightRight = Math.cos(angle) * boHeight
        ctx.beginPath()
        ctx.moveTo(0, posHeight + deltaHeight)
        ctx.moveTo(0, posHeight + deltaHeight)
        ctx.bezierCurveTo(
          canvas.width / 2,
          posHeight + deltaHeight - boHeight,
          canvas.width / 2,
          posHeight + deltaHeightRight - boHeight,
          canvas.width,
          posHeight + deltaHeightRight
        )
        ctx.lineTo(canvas.width, canvas.height)
        ctx.lineTo(0, canvas.height)
        ctx.lineTo(0, posHeight + deltaHeight)
        ctx.closePath()
        ctx.fill()
      }
      window.requestAnimFrame(loop)
    }
    loop()
  }
}
</script>

<style scoped lang="scss">
.btn_box{
  display: flex;
  justify-content:space-between;
  div{
    color:#2d8cf0;
    cursor: pointer;
  }
}
.login-wrapper {
  height: 100%;
}
