    padding: 20px 35px;
    border: 1px solid #eee;
    transform: translate(-50%, -50%);
  &::before,
  &::after {
    position: absolute;
    height: 12px;
    content: '';
  }
  &::before {
    left: 4px;
    right: 4px;
    top: -12px;
    z-index: 2;
    background-color: #f5f5f5;
  }
  &::after {
    left: 10px;
    right: 10px;
    top: -22px;
    z-index: 1;
    background-color: #f0f0f0;
  }
}
.login-title {
  padding-bottom: 15px;
