<template>
  <div>
    <div class="value_box " style="margin-bottom: 20px;">
      <div class="value_box">
        <span>公司名称：</span> <el-input class="w200" v-model="OptionData.companyName"  placeholder="请输入内容"></el-input>
      </div>
       <div class="value_box">
        <span>法定代表人：</span> <el-input class="w200" v-model="OptionData.representative"  placeholder="请输入内容"></el-input>
      </div>
       <div class="value_box">
        <span>公司性质：</span> <el-input class="w200" v-model="OptionData.companyType" placeholder="请输入内容"></el-input>
      </div>
       <div class="value_box">
        <span>社会信用代码：</span> <el-input class="w200" v-model="OptionData.socialCreditCode" oninput ="value=value.replace(/[^0-9.]/g,'')"  placeholder="请输入内容"></el-input>
      </div>
       <el-button type="primary" @click="getData">查询</el-button>
    </div>
  <el-table
    :data="tableData"
    style="width: 100%"
    >
    <el-table-column
      prop="companyName"
      label="公司名称"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="socialCreditCode"
      label="社会信用代码"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="companyType"  align="center"
      label="公司性质"
      >
    </el-table-column>
    <el-table-column
      prop="email"  align="center"
      label="电子邮箱"
      >
    </el-table-column>
    <el-table-column
      prop="statusFlag"  align="center"
      label="公司状态"
      >
        <template slot-scope="item">
          <div>{{item.row.statusFlag===1?'启用':'停用'}}</div>
      </template>
    </el-table-column>
    <el-table-column
      prop="representative"  align="center"
      label="法定代表人"
      >
    </el-table-column>
    <el-table-column
      prop="contactsName"  align="center"
      label="联系人"
      >
    </el-table-column>
    <el-table-column
      prop="contactsPhone"  align="center"
      label="联系手机"
      >
    </el-table-column>
     <el-table-column label="操作" width="200" align="center">
      <template slot-scope="item">
        <el-button type="danger" v-if="item.row.statusFlag===1"
          size="mini" @click="operate(item.row.id,2)"
          >停用</el-button>
           <el-button type="primary"  @click="operate(item.row.id,1)" v-if="item.row.statusFlag===2"
          size="mini"
          >启用</el-button>
          <el-button type="danger"
          size="mini" @click="open(item.row.id)"
          >删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="fy">
    <el-pagination
  background  @current-change="pageClick"
  layout="prev, pager, next"
   :total="total">
</el-pagination>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      total: 0,
      tableData: [

      ],
      OptionData: {
        pageNum: 1,
        pageSize: 10,
        statusFlag: [1, 2]
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    pageClick (value) {
      this.OptionData.pageNum = value
      this.getData()
    },
    operate (id, value) {
      this.$axios.post('/seaman-recruit/company/updateById', { statusFlag: value, id: id }).then(res => {
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: '操作成功!'
          })
          this.getData()
          // this.$router.back()
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    open (value) {
      this.$confirm('此操作将永久删除该公司, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post('/seaman-recruit/company/delete', [value]).then(res => {
          if (res.data.code === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.getData()
          } else {
            this.$message.error(res.data.data)
          }
        })
      }).catch(() => {

      })
    },
    getData () {
      this.$axios.post('/seaman-recruit/company/queryByPage', this.OptionData).then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.records
          this.total = res.data.data.total
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    formatter (row, column) {
      return row.address
    }
  }
}
</script>

<style lang="scss" scoped>
.value_box{
  display: flex;
  align-items: baseline;
}

::v-deep.el-table th.el-table__cell{
  background: #F8F8F9;
}
.w200{
  width: 140px;
  margin: 0 10px;
}
</style>
